import React, {useEffect} from "react";
import {useLocation} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {
    getCurrentLocation,
    selectBricksLoading,
    selectCurrentLocation
} from "../../../store/reducers/bricks.reducer";
import style from "./bricksLocation.module.scss"
import {MarkCard} from "../MarkCard/MarkCard";


export const BricksLocation = () => {
    const urlLocation = useLocation();
    console.log(urlLocation);
    // @ts-ignore
    const locationId = urlLocation.state.locationId;

    const currentBricksLocation = useSelector(selectCurrentLocation);
    const loading = useSelector(selectBricksLoading);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getCurrentLocation(locationId));
    }, []);


    return (
        <div className={style.bricksLocation}>
            {
                !loading && currentBricksLocation &&
                <>
                    <p className={style.name}>{currentBricksLocation.name}</p>
                    <div className={style.markCards}>
                        {
                            // @ts-ignore
                            currentBricksLocation.marks.map(mark => <MarkCard key={mark.id} mark={mark} className={style.card}/>)
                        }
                    </div>
                </>
            }

        </div>
    )
}